"use client"

import { Section } from "./ui/section"
import { MapPin, Navigation, Sparkles, Phone } from "lucide-react"
import { useRef, useEffect } from "react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

export function Location() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      gsap.from(".location-content", {
        x: -80,
        opacity: 0,
        duration: 1,
        ease: "power3.out",
        scrollTrigger: { trigger: ".location-content", start: "top 80%" },
      })

      gsap.from(".location-item", {
        y: 30,
        opacity: 0,
        stagger: 0.15,
        duration: 0.8,
        ease: "power2.out",
        scrollTrigger: { trigger: ".location-items", start: "top 85%" },
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="location" className="p-0 relative overflow-hidden">
      <div ref={sectionRef} className="grid lg:grid-cols-2 min-h-[600px]">
        <div className="bg-gradient-to-br from-slate-950 via-teal-950 to-slate-900 p-12 lg:p-16 flex flex-col justify-center relative overflow-hidden">
          <div className="absolute inset-0 opacity-10">
            <div className="absolute top-0 right-0 w-96 h-96 bg-teal-500 rounded-full blur-[120px]" />
            <div className="absolute bottom-0 left-0 w-96 h-96 bg-cyan-500 rounded-full blur-[120px]" />
          </div>

          <div className="location-content max-w-md mx-auto w-full relative z-10">
            <div className="inline-flex items-center gap-2 px-6 py-3 rounded-full bg-teal-500/10 backdrop-blur-sm border border-teal-500/20 mb-8">
              <Sparkles className="w-5 h-5 text-teal-400" />
              <span className="text-sm font-bold tracking-wider uppercase text-teal-300">Find Us</span>
            </div>

            <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl font-bold text-white mb-10 leading-tight">
              <span className="bg-gradient-to-r from-teal-200 via-cyan-300 to-teal-200 bg-clip-text text-transparent">
                Visit Our HQ
              </span>
            </h2>

            <div className="location-items space-y-8">
              {[
                { icon: MapPin, title: "Main Office", desc: "123 Poolside Avenue\nSunnyvale, CA 94086\nUnited States" },
                { icon: Navigation, title: "Service Area", desc: "Serving the greater Bay Area and surrounding communities within a 50-mile radius." },
                { icon: Phone, title: "Contact", desc: "+1 (555) 123-4567\nhello@dogepools.com" },
              ].map((item, i) => (
                <div key={i} className="location-item group flex items-start gap-5">
                  <div className="w-16 h-16 bg-gradient-to-br from-teal-500 to-cyan-500 rounded-2xl flex items-center justify-center shrink-0 group-hover:scale-110 group-hover:rotate-6 transition-all duration-500 shadow-xl shadow-teal-500/40">
                    <item.icon className="w-8 h-8 text-white" />
                  </div>
                  <div>
                    <h3 className="text-xl font-bold text-white mb-2 font-['Playfair_Display']">{item.title}</h3>
                    <p className="text-teal-200/70 leading-relaxed whitespace-pre-line font-['Montserrat']">{item.desc}</p>
                  </div>
                </div>
              ))}

              <button className="mt-8 w-full bg-gradient-to-r from-teal-600 to-cyan-500 text-white py-5 rounded-xl font-bold hover:shadow-2xl hover:shadow-teal-500/60 transition-all flex items-center justify-center gap-3 group hover:scale-105 text-lg">
                Get Directions
                <Navigation className="w-6 h-6 group-hover:translate-x-1 transition-transform" />
              </button>
            </div>
          </div>
        </div>

        <div className="relative h-full min-h-[600px] w-full bg-slate-900">
          <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d100939.98555098464!2d-122.50764017944336!3d37.757814996609724!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x80859a6d00690021%3A0x4a501367f076adff!2sSan%20Francisco%2C%20CA!5e0!3m2!1sen!2sus!4v1647000000000!5m2!1sen!2sus" width="100%" height="100%" style={{ border: 0, filter: "grayscale(60%) contrast(1.1) brightness(0.85) hue-rotate(140deg)" }} allowFullScreen loading="lazy" referrerPolicy="no-referrer-when-downgrade" />
          <div className="absolute inset-0 pointer-events-none bg-gradient-to-l from-transparent to-slate-950/20" />
          <div className="absolute inset-0 pointer-events-none border-l-2 border-teal-500/30" />
        </div>
      </div>
    </Section>
  )
}
