"use client"

import { Section } from "./ui/section"
import { useRef, useEffect } from "react"
import { Send, Phone, Mail, Clock, MapPin, Sparkles, Waves, Droplets } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

export function Contact() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      // 3D Bottom-to-Top Reveals
      gsap.from(".contact-badge", {
        y: 100,
        opacity: 0,
        rotationX: 90,
        transformPerspective: 1000,
        duration: 1.5,
        ease: "power4.out",
        scrollTrigger: { trigger: ".contact-badge", start: "top 85%" },
      })

      gsap.from(".contact-info", {
        x: -100,
        opacity: 0,
        rotationY: -15,
        transformPerspective: 1200,
        duration: 1.4,
        ease: "power3.out",
        scrollTrigger: { trigger: ".contact-info", start: "top 80%" },
      })

      gsap.from(".contact-form", {
        x: 100,
        opacity: 0,
        rotationY: 15,
        transformPerspective: 1200,
        duration: 1.4,
        ease: "power3.out",
        scrollTrigger: { trigger: ".contact-form", start: "top 80%" },
      })

      gsap.from(".contact-item", {
        y: 60,
        opacity: 0,
        rotationX: 45,
        transformPerspective: 800,
        stagger: 0.18,
        duration: 1,
        ease: "back.out(1.5)",
        scrollTrigger: { trigger: ".contact-items", start: "top 85%" },
      })

      // Parallax on scroll
      gsap.to(".contact-glow", {
        y: -80,
        ease: "none",
        scrollTrigger: {
          trigger: ".contact-glow",
          start: "top bottom",
          end: "bottom top",
          scrub: 1.5,
        },
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="contact" className="bg-gradient-to-br from-slate-950 via-cyan-950 to-teal-950 text-white relative overflow-hidden">
      {/* 3D Glow Effect & Camera Zoom Feel */}
      <div className="contact-glow absolute inset-0 opacity-30">
        <div className="absolute top-0 left-1/4 w-[900px] h-[900px] bg-gradient-to-br from-teal-400 to-cyan-400 rounded-full blur-[200px] animate-pulse" style={{ animationDuration: '5s' }} />
        <div className="absolute bottom-0 right-1/4 w-[800px] h-[800px] bg-gradient-to-br from-cyan-400 to-teal-400 rounded-full blur-[180px] animate-pulse" style={{ animationDuration: '6s', animationDelay: '1.5s' }} />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[700px] h-[700px] bg-teal-300 rounded-full blur-[160px] opacity-40" />
      </div>

      {/* 3D Splash Illustration - Floating Droplets */}
      <div className="absolute inset-0 pointer-events-none overflow-hidden">
        <div className="absolute top-20 left-10 w-8 h-8 bg-cyan-400/30 rounded-full blur-md animate-bounce" style={{ animationDuration: '3s', animationDelay: '0s' }} />
        <div className="absolute top-40 right-20 w-6 h-6 bg-teal-400/40 rounded-full blur-sm animate-bounce" style={{ animationDuration: '4s', animationDelay: '1s' }} />
        <div className="absolute bottom-32 left-1/4 w-10 h-10 bg-cyan-300/25 rounded-full blur-lg animate-bounce" style={{ animationDuration: '3.5s', animationDelay: '0.5s' }} />
        <div className="absolute bottom-20 right-1/3 w-7 h-7 bg-teal-300/35 rounded-full blur-md animate-bounce" style={{ animationDuration: '4.5s', animationDelay: '1.5s' }} />
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="grid lg:grid-cols-2 gap-16">
          <div className="contact-info">
            <div className="contact-badge inline-flex items-center gap-3 px-8 py-4 rounded-full bg-gradient-to-r from-cyan-500/25 to-teal-500/25 backdrop-blur-2xl border-2 border-cyan-400/40 mb-10 shadow-2xl shadow-cyan-500/40">
              <Droplets className="w-6 h-6 text-cyan-300 animate-pulse" />
              <span className="text-sm font-bold tracking-[0.3em] uppercase bg-gradient-to-r from-cyan-200 to-teal-200 bg-clip-text text-transparent">Get In Touch</span>
            </div>

            <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl xl:text-8xl font-bold mb-8 leading-[1.1]">
              <span className="block bg-gradient-to-r from-cyan-100 via-teal-100 to-cyan-100 bg-clip-text text-transparent drop-shadow-[0_0_60px_rgba(6,182,212,0.7)]">
                Transform Your
              </span>
              <span className="block bg-gradient-to-r from-teal-100 via-cyan-100 to-teal-100 bg-clip-text text-transparent drop-shadow-[0_0_60px_rgba(20,184,166,0.7)] mt-2">
                Aquatic Paradise
              </span>
            </h2>

            <p className="text-cyan-100/90 text-xl md:text-2xl mb-14 leading-relaxed font-['Montserrat'] font-light">
              Ready to elevate your pool experience? Our certified specialists are ready to deliver a complimentary consultation and custom quote.
            </p>

            <div className="contact-items space-y-8">
              {[
                { icon: Phone, label: "Call Us", value: "+1 (555) 123-4567", color: "from-cyan-500 to-teal-500" },
                { icon: Mail, label: "Email Us", value: "hello@dogepools.com", color: "from-teal-500 to-cyan-500" },
                { icon: Clock, label: "Working Hours", value: "Mon - Sat: 8am - 6pm", color: "from-cyan-400 to-teal-400" },
                { icon: MapPin, label: "Service Area", value: "Greater Metro Area", color: "from-teal-400 to-cyan-400" },
              ].map((item, i) => (
                <div key={i} className="contact-item group">
                  <div className="flex items-center gap-6 bg-white/5 backdrop-blur-xl rounded-2xl p-6 border-2 border-cyan-500/20 hover:border-cyan-400/50 transition-all duration-500 hover:bg-white/10 hover:shadow-2xl hover:shadow-cyan-500/40 hover:-translate-y-2 cursor-pointer">
                    <div className={`w-20 h-20 bg-gradient-to-br ${item.color} rounded-2xl flex items-center justify-center group-hover:scale-110 group-hover:rotate-12 transition-all duration-500 shadow-2xl shadow-cyan-500/40`}>
                      <item.icon className="w-9 h-9 text-white" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm text-cyan-300 uppercase tracking-[0.2em] font-bold mb-2">{item.label}</p>
                      <p className="text-xl md:text-2xl font-semibold text-white group-hover:text-cyan-200 transition-colors font-['Montserrat']">{item.value}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="contact-form">
            <div className="bg-gradient-to-br from-white/10 to-white/5 backdrop-blur-2xl p-10 md:p-12 rounded-[2rem] shadow-2xl border-2 border-cyan-400/30 hover:border-cyan-400/50 transition-all duration-700 hover:shadow-cyan-500/40">
              <form className="space-y-7">
                <div className="grid md:grid-cols-2 gap-6">
                  {["First Name", "Last Name"].map((label) => (
                    <div key={label} className="space-y-3">
                      <label className="text-sm font-bold text-cyan-200 uppercase tracking-[0.2em]">{label}</label>
                      <input 
                        type="text" 
                        className="w-full px-6 py-5 rounded-2xl bg-white/10 backdrop-blur-sm border-2 border-cyan-500/30 focus:border-cyan-400 focus:ring-4 focus:ring-cyan-400/20 outline-none transition-all text-white placeholder-white/40 hover:bg-white/15" 
                        placeholder={label === "First Name" ? "John" : "Doe"} 
                      />
                    </div>
                  ))}
                </div>

                <div className="space-y-3">
                  <label className="text-sm font-bold text-cyan-200 uppercase tracking-[0.2em]">Email Address</label>
                  <input 
                    type="email" 
                    className="w-full px-6 py-5 rounded-2xl bg-white/10 backdrop-blur-sm border-2 border-cyan-500/30 focus:border-cyan-400 focus:ring-4 focus:ring-cyan-400/20 outline-none transition-all text-white placeholder-white/40 hover:bg-white/15" 
                    placeholder="john@example.com" 
                  />
                </div>

                <div className="space-y-3">
                  <label className="text-sm font-bold text-cyan-200 uppercase tracking-[0.2em]">Phone Number</label>
                  <input 
                    type="tel" 
                    className="w-full px-6 py-5 rounded-2xl bg-white/10 backdrop-blur-sm border-2 border-cyan-500/30 focus:border-cyan-400 focus:ring-4 focus:ring-cyan-400/20 outline-none transition-all text-white placeholder-white/40 hover:bg-white/15" 
                    placeholder="+1 (555) 000-0000" 
                  />
                </div>

                <div className="space-y-3">
                  <label className="text-sm font-bold text-cyan-200 uppercase tracking-[0.2em]">Service Type</label>
                  <select className="w-full px-6 py-5 rounded-2xl bg-white/10 backdrop-blur-sm border-2 border-cyan-500/30 focus:border-cyan-400 focus:ring-4 focus:ring-cyan-400/20 outline-none transition-all text-white hover:bg-white/15">
                    <option className="bg-slate-900">Weekly Maintenance</option>
                    <option className="bg-slate-900">One-time Cleaning</option>
                    <option className="bg-slate-900">Pool Repair</option>
                    <option className="bg-slate-900">Pool Construction</option>
                  </select>
                </div>

                <div className="space-y-3">
                  <label className="text-sm font-bold text-cyan-200 uppercase tracking-[0.2em]">Message</label>
                  <textarea 
                    rows={5} 
                    className="w-full px-6 py-5 rounded-2xl bg-white/10 backdrop-blur-sm border-2 border-cyan-500/30 focus:border-cyan-400 focus:ring-4 focus:ring-cyan-400/20 outline-none transition-all text-white placeholder-white/40 resize-none hover:bg-white/15" 
                    placeholder="Tell us about your pool vision..." 
                  />
                </div>

                <button 
                  type="submit" 
                  className="group w-full bg-gradient-to-r from-cyan-500 via-teal-500 to-cyan-500 text-white font-bold py-6 rounded-2xl hover:shadow-2xl hover:shadow-cyan-500/70 transition-all transform hover:scale-[1.02] flex items-center justify-center gap-4 text-lg md:text-xl relative overflow-hidden"
                >
                  <div className="absolute inset-0 bg-gradient-to-r from-teal-400 to-cyan-400 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                  <span className="relative z-10">Send Your Request</span>
                  <Send className="w-6 h-6 relative z-10 group-hover:translate-x-2 group-hover:-translate-y-1 transition-transform duration-500" />
                  <div className="absolute inset-0 -translate-x-full group-hover:translate-x-full transition-transform duration-1000 bg-gradient-to-r from-transparent via-white/30 to-transparent" />
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Section>
  )
}
