"use client"

import { Facebook, Instagram, Twitter, Linkedin, Sparkles, Heart, Waves, Mail, Phone, MapPin } from "lucide-react"
import Image from "next/image"
import { useEffect, useRef } from "react"
import gsap from "gsap"

export function Footer() {
  const canvasRef = useRef<HTMLCanvasElement>(null)

  // Floating Particles Animation
  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    canvas.width = canvas.offsetWidth
    canvas.height = canvas.offsetHeight

    const particles: Array<{
      x: number
      y: number
      size: number
      speedY: number
      opacity: number
    }> = []

    for (let i = 0; i < 30; i++) {
      particles.push({
        x: Math.random() * canvas.width,
        y: Math.random() * canvas.height,
        size: Math.random() * 3 + 1,
        speedY: Math.random() * 0.5 + 0.2,
        opacity: Math.random() * 0.5 + 0.2,
      })
    }

    function animate() {
      if (!ctx || !canvas) return
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      particles.forEach((particle) => {
        particle.y -= particle.speedY
        if (particle.y < 0) particle.y = canvas.height

        ctx.fillStyle = `rgba(20, 184, 166, ${particle.opacity})`
        ctx.beginPath()
        ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2)
        ctx.fill()
      })

      requestAnimationFrame(animate)
    }

    animate()

    const handleResize = () => {
      canvas.width = canvas.offsetWidth
      canvas.height = canvas.offsetHeight
    }

    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  return (
    <footer className="bg-gradient-to-br from-slate-950 via-teal-950 to-cyan-950 text-slate-300 py-20 border-t-4 border-teal-500/30 relative overflow-hidden">
      {/* Floating Background Particles */}
      <canvas ref={canvasRef} className="absolute inset-0 w-full h-full pointer-events-none" />

      {/* Animated Wave Border Top */}
      <div className="absolute top-0 left-0 right-0 h-1 bg-gradient-to-r from-transparent via-cyan-400 to-transparent animate-pulse" />
      
      {/* Glowing Background */}
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-0 left-1/4 w-[600px] h-[600px] bg-teal-400 rounded-full blur-[150px] animate-pulse" style={{ animationDuration: '6s' }} />
        <div className="absolute bottom-0 right-1/4 w-[500px] h-[500px] bg-cyan-400 rounded-full blur-[140px] animate-pulse" style={{ animationDuration: '7s', animationDelay: '2s' }} />
      </div>

      {/* Neon Grid Overlay */}
      <div className="absolute inset-0 opacity-5" style={{
        backgroundImage: `linear-gradient(rgba(6, 182, 212, 0.3) 1px, transparent 1px),
                         linear-gradient(90deg, rgba(6, 182, 212, 0.3) 1px, transparent 1px)`,
        backgroundSize: '60px 60px'
      }} />

      <div className="container mx-auto px-6 relative z-10">
        <div className="grid md:grid-cols-4 gap-16 mb-16">
          <div className="col-span-1 md:col-span-2">
            {/* Bigger Glowing Logo */}
            <div className="flex items-center gap-6 mb-8 group">
              <div className="relative w-24 h-24 rounded-3xl ring-4 ring-teal-400/50 shadow-2xl shadow-teal-500/50 group-hover:ring-cyan-400/70 group-hover:shadow-cyan-500/70 transition-all duration-700 group-hover:scale-110 group-hover:rotate-6">
                <Image 
                  src="/DOGEPOOL.jpg" 
                  alt="Doge Pools Logo" 
                  fill 
                  className="object-cover rounded-3xl" 
                />
                {/* Glowing Aura */}
                <div className="absolute inset-0 rounded-3xl bg-gradient-to-br from-teal-400 to-cyan-400 blur-2xl opacity-0 group-hover:opacity-60 transition-opacity duration-700 -z-10" />
              </div>
              <div className="flex flex-col">
                <span className="text-4xl font-['Playfair_Display'] font-bold bg-gradient-to-r from-teal-100 to-cyan-100 bg-clip-text text-transparent drop-shadow-[0_0_20px_rgba(20,184,166,0.8)]">DOGE POOLS</span>
                <span className="text-xs text-cyan-400 tracking-[0.3em] font-bold mt-1">PREMIUM AQUATIC SERVICES</span>
              </div>
            </div>
            
            <p className="max-w-md leading-relaxed mb-10 text-base text-teal-100/80 font-['Montserrat']">
              Elevating pool construction, maintenance, and repair to an art form. We deliver luxury, precision, and unparalleled excellence to transform your backyard into an aquatic paradise.
            </p>
            
            {/* Social Icons with Glow */}
            <div className="flex gap-5">
              {[
                { Icon: Facebook, color: "from-teal-500 to-cyan-500" },
                { Icon: Instagram, color: "from-cyan-500 to-teal-500" },
                { Icon: Twitter, color: "from-teal-400 to-cyan-400" },
                { Icon: Linkedin, color: "from-cyan-400 to-teal-400" },
              ].map(({ Icon, color }, i) => (
                <a 
                  key={i} 
                  href="#" 
                  className="group/social relative w-14 h-14 rounded-2xl bg-gradient-to-br from-white/5 to-white/10 backdrop-blur-sm border-2 border-teal-500/30 flex items-center justify-center hover:border-cyan-400/60 transition-all duration-500 hover:scale-125 hover:rotate-12 cursor-pointer"
                >
                  <Icon className="w-6 h-6 text-teal-300 group-hover/social:text-white transition-colors relative z-10" />
                  {/* Neon Glow on Hover */}
                  <div className={`absolute inset-0 rounded-2xl bg-gradient-to-br ${color} opacity-0 group-hover/social:opacity-100 transition-opacity duration-500`} />
                  <div className={`absolute inset-0 rounded-2xl bg-gradient-to-br ${color} blur-xl opacity-0 group-hover/social:opacity-70 transition-opacity duration-500`} />
                </a>
              ))}
            </div>
          </div>

          <div>
            <h4 className="text-white font-bold mb-8 text-xl flex items-center gap-3 font-['Playfair_Display']">
              <Waves className="w-6 h-6 text-cyan-400" />
              Quick Links
            </h4>
            <ul className="space-y-5 font-['Montserrat']">
              {["Home", "About Us", "Services", "Gallery", "Contact"].map((item) => (
                <li key={item}>
                  <a 
                    href={`#${item.toLowerCase().replace(" ", "-")}`} 
                    className="group inline-flex items-center gap-2 hover:text-cyan-300 transition-all duration-300 text-teal-200/80 hover:translate-x-2"
                  >
                    <span className="w-0 h-0.5 bg-gradient-to-r from-cyan-400 to-teal-400 group-hover:w-4 transition-all duration-300 rounded-full" />
                    {item}
                  </a>
                </li>
              ))}
            </ul>
          </div>

          <div>
            <h4 className="text-white font-bold mb-8 text-xl flex items-center gap-3 font-['Playfair_Display']">
              <Sparkles className="w-6 h-6 text-cyan-400" />
              Services
            </h4>
            <ul className="space-y-5 font-['Montserrat']">
              {["Pool Maintenance", "Repairs & Upgrades", "New Construction", "Water Testing", "Pool Renovation"].map((item) => (
                <li key={item}>
                  <a 
                    href="#services" 
                    className="group inline-flex items-center gap-2 hover:text-cyan-300 transition-all duration-300 text-teal-200/80 hover:translate-x-2"
                  >
                    <span className="w-0 h-0.5 bg-gradient-to-r from-cyan-400 to-teal-400 group-hover:w-4 transition-all duration-300 rounded-full" />
                    {item}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        </div>

        {/* Bottom Section with Wave Border */}
        <div className="pt-10 border-t-2 border-gradient-to-r from-transparent via-teal-500/30 to-transparent relative">
          {/* Animated Wave Line */}
          <div className="absolute top-0 left-0 right-0 h-0.5 bg-gradient-to-r from-transparent via-cyan-400 to-transparent animate-pulse" />
          
          <div className="flex flex-col md:flex-row justify-between items-center gap-6 text-sm font-['Montserrat']">
            <p className="text-teal-200/70 flex items-center gap-2 text-base">
              &copy; {new Date().getFullYear()} <span className="font-bold text-cyan-300">Doge Pools</span>. 
              Crafted with <Heart className="w-5 h-5 text-cyan-400 fill-cyan-400 inline animate-pulse mx-1" /> and <Waves className="w-5 h-5 text-teal-400 inline animate-bounce mx-1" /> for aquatic excellence.
            </p>
            <div className="flex gap-10">
              <a href="#" className="hover:text-cyan-300 transition-colors text-teal-200/70 hover:underline underline-offset-4">Privacy Policy</a>
              <a href="#" className="hover:text-cyan-300 transition-colors text-teal-200/70 hover:underline underline-offset-4">Terms of Service</a>
              <a href="#" className="hover:text-cyan-300 transition-colors text-teal-200/70 hover:underline underline-offset-4">Sitemap</a>
            </div>
          </div>
        </div>
      </div>

      {/* Bottom Glow Effect */}
      <div className="absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r from-teal-500 via-cyan-400 to-teal-500 opacity-50 blur-sm" />
    </footer>
  )
}
