"use client"

import { Section } from "./ui/section"
import { useRef, useEffect } from "react"
import { Droplets, Zap, Scale, Sparkles, Trash2, Filter, Brush, Waves, Search } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

const services = [
  { icon: Droplets, title: "Water Testing", description: "Precise chemical analysis using advanced digital photometers." },
  { icon: Zap, title: "Pool Shocking", description: "Eliminate bacteria and algae with premium shock treatments." },
  { icon: Scale, title: "Water Balance", description: "Perfect pH and alkalinity alignment for swimmer comfort." },
  { icon: Sparkles, title: "Sanitize", description: "Complete sanitization ensuring a safe swimming environment." },
  { icon: Trash2, title: "Empty Baskets", description: "Thorough cleaning of skimmer and pump baskets." },
  { icon: Filter, title: "Filter Cleaning", description: "Deep cleaning to maintain optimal filtration efficiency." },
  { icon: Brush, title: "Brush Pool", description: "Scrubbing walls and tiles to prevent algae buildup." },
  { icon: Waves, title: "Skim Surface", description: "Removal of all floating debris for a glass-like finish." },
  { icon: Search, title: "Vacuum Pool", description: "Precision vacuuming to remove sediment and debris." },
]

export function Services() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      // 3D Title Reveal - Bottom to Top
      gsap.from(".services-badge", {
        y: 100,
        opacity: 0,
        rotationX: 90,
        transformPerspective: 1000,
        duration: 1.5,
        ease: "power4.out",
        scrollTrigger: { trigger: ".services-badge", start: "top 85%" },
      })

      gsap.from(".services-title", {
        y: 120,
        opacity: 0,
        scale: 0.8,
        rotationX: 90,
        transformPerspective: 1500,
        duration: 1.8,
        ease: "power4.out",
        scrollTrigger: { trigger: ".services-title", start: "top 80%" },
      })

      // Parallax Stagger Cards
      gsap.from(".service-card", {
        y: 150,
        opacity: 0,
        rotationX: -60,
        transformPerspective: 1200,
        stagger: {
          each: 0.12,
          from: "start",
        },
        duration: 1.2,
        ease: "power4.out",
        scrollTrigger: { 
          trigger: ".services-grid", 
          start: "top 75%",
        },
      })

      // Parallax depth on scroll
      document.querySelectorAll(".service-card").forEach((card, index) => {
        gsap.to(card, {
          y: -40 * (index % 2 === 0 ? 1 : -1),
          ease: "none",
          scrollTrigger: {
            trigger: card,
            start: "top bottom",
            end: "bottom top",
            scrub: 1.5,
          },
        })
      })

      // 3D Hover Effects with GSAP
      document.querySelectorAll(".service-card").forEach((card) => {
        const icon = card.querySelector(".service-icon")
        
        card.addEventListener("mouseenter", function () {
          gsap.to(card, { 
            y: -20, 
            scale: 1.08, 
            rotationY: 8,
            rotationX: -5,
            duration: 0.6, 
            ease: "power3.out" 
          })
          gsap.to(icon, {
            scale: 1.2,
            rotationZ: 360,
            duration: 0.8,
            ease: "back.out(2)"
          })
        })
        
        card.addEventListener("mouseleave", function () {
          gsap.to(card, { 
            y: 0, 
            scale: 1, 
            rotationY: 0,
            rotationX: 0,
            duration: 0.6, 
            ease: "power3.out" 
          })
          gsap.to(icon, {
            scale: 1,
            rotationZ: 0,
            duration: 0.6,
            ease: "power2.out"
          })
        })
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="services" className="bg-gradient-to-br from-white via-teal-50/30 to-white relative overflow-hidden">
      {/* Floating Geometric Shapes - CSS Only */}
      <div className="absolute inset-0 pointer-events-none overflow-hidden opacity-10">
        {[...Array(8)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full bg-gradient-to-br from-teal-400 to-cyan-400 blur-xl"
            style={{
              width: `${60 + Math.random() * 100}px`,
              height: `${60 + Math.random() * 100}px`,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animation: `float ${4 + Math.random() * 6}s ease-in-out infinite`,
              animationDelay: `${Math.random() * 3}s`,
            }}
          />
        ))}
      </div>

      {/* Animated Grid Pattern */}
      <div className="absolute inset-0 opacity-[0.03]">
        <div className="absolute inset-0" style={{ backgroundImage: `url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%2314b8a6' fill-opacity='1'%3E%3Cpath d='M50 50c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10s-10-4.477-10-10 4.477-10 10-10zM10 10c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10S0 25.523 0 20s4.477-10 10-10zm10 8c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm40 40c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E")` }} />
      </div>

      {/* Soft Glow Lighting */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-0 left-1/4 w-[600px] h-[600px] bg-teal-400 rounded-full blur-[150px] opacity-10 animate-pulse" />
        <div className="absolute bottom-0 right-1/4 w-[500px] h-[500px] bg-cyan-400 rounded-full blur-[140px] opacity-10 animate-pulse" style={{ animationDelay: '1s' }} />
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="services-title text-center max-w-4xl mx-auto mb-24">
          <div className="services-badge inline-flex items-center gap-3 px-8 py-4 rounded-full bg-gradient-to-r from-teal-500/20 to-cyan-500/20 backdrop-blur-xl border-2 border-teal-500/30 mb-10 shadow-xl shadow-teal-500/20">
            <Sparkles className="w-6 h-6 text-teal-600 animate-pulse" />
            <span className="text-sm font-bold tracking-[0.3em] uppercase bg-gradient-to-r from-teal-700 to-cyan-700 bg-clip-text text-transparent">Our Services</span>
          </div>

          <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl xl:text-8xl font-bold mb-8 leading-[1.1]">
            <span className="block bg-gradient-to-r from-teal-600 via-cyan-600 to-teal-600 bg-clip-text text-transparent drop-shadow-[0_0_30px_rgba(20,184,166,0.3)]">
              Unparalleled Pool
            </span>
            <span className="block bg-gradient-to-r from-cyan-600 via-teal-600 to-cyan-600 bg-clip-text text-transparent drop-shadow-[0_0_30px_rgba(6,182,212,0.3)] mt-2">
              Excellence
            </span>
          </h2>
          <p className="text-xl md:text-2xl text-slate-700 leading-relaxed font-['Montserrat'] font-light">
            Comprehensive care packages engineered for perfection, delivered with precision.
          </p>
        </div>

        <div className="services-grid grid sm:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-10">
          {services.map((service, index) => (
            <div
              key={index}
              className="service-card group bg-white/80 backdrop-blur-xl p-10 md:p-12 rounded-[2rem] hover:shadow-2xl hover:shadow-teal-500/30 transition-all duration-700 border-2 border-slate-200/50 hover:border-teal-400/50 relative overflow-hidden cursor-pointer"
              style={{ perspective: "1500px", transformStyle: "preserve-3d" }}
            >
              {/* Glass Morphism Overlay */}
              <div className="absolute inset-0 bg-gradient-to-br from-teal-500/0 via-cyan-500/0 to-teal-500/0 group-hover:from-teal-500/10 group-hover:via-cyan-500/10 group-hover:to-teal-500/10 transition-all duration-700 rounded-[2rem]" />
              
              {/* 3D Floating Icon */}
              <div className="service-icon relative mb-8">
                <div className="w-24 h-24 bg-gradient-to-br from-teal-500 to-cyan-500 rounded-3xl flex items-center justify-center shadow-2xl shadow-teal-500/40 group-hover:shadow-teal-500/70 transition-all duration-700 relative">
                  <service.icon className="w-12 h-12 text-white" />
                  {/* Neon Glow */}
                  <div className="absolute inset-0 rounded-3xl bg-gradient-to-br from-teal-400 to-cyan-400 blur-xl opacity-0 group-hover:opacity-60 transition-opacity duration-700" />
                </div>
              </div>

              <h3 className="text-2xl md:text-3xl font-bold text-slate-900 mb-5 group-hover:text-teal-600 transition-colors duration-500 font-['Playfair_Display'] relative z-10">
                {service.title}
              </h3>
              <p className="text-base md:text-lg text-slate-600 leading-relaxed font-['Montserrat'] relative z-10 group-hover:text-slate-700 transition-colors">
                {service.description}
              </p>

              {/* Corner Accent */}
              <div className="absolute top-0 right-0 w-32 h-32 bg-gradient-to-br from-transparent via-teal-500/0 to-teal-500/0 group-hover:via-teal-500/10 group-hover:to-cyan-500/20 rounded-bl-full transition-all duration-700" />
              
              {/* Bottom Glow Line */}
              <div className="absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r from-transparent via-teal-500 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-700" />
            </div>
          ))}
        </div>

        <div className="text-center mt-24">
          <a
            href="#contact"
            className="group inline-flex items-center gap-4 px-14 py-7 bg-gradient-to-r from-teal-600 via-cyan-500 to-teal-600 text-white text-xl font-bold rounded-full hover:shadow-2xl hover:shadow-teal-500/70 hover:scale-110 transition-all duration-700 relative overflow-hidden"
          >
            <div className="absolute inset-0 bg-gradient-to-r from-cyan-400 to-teal-400 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
            <span className="relative z-10">Request Your Custom Quote</span>
            <Sparkles className="w-7 h-7 relative z-10 group-hover:rotate-180 transition-transform duration-700" />
            <div className="absolute inset-0 -translate-x-full group-hover:translate-x-full transition-transform duration-1000 bg-gradient-to-r from-transparent via-white/30 to-transparent" />
          </a>
        </div>
      </div>
    </Section>
  )
}
