"use client"

import { Section } from "./ui/section"
import { useRef, useEffect, useState } from "react"
import { Play, Sparkles, ZoomIn, Eye } from "lucide-react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

const videos = [
  "https://videos.pexels.com/video-files/3205916/3205916-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/4325457/4325457-hd_1920_1080_30fps.mp4",
  "https://videos.pexels.com/video-files/6110556/6110556-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/3205916/3205916-hd_1920_1080_25fps.mp4",
  "https://videos.pexels.com/video-files/4325457/4325457-hd_1920_1080_30fps.mp4",
  "https://videos.pexels.com/video-files/6110556/6110556-hd_1920_1080_25fps.mp4",
]

export function Gallery() {
  const sectionRef = useRef<HTMLDivElement>(null)
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      // 3D Title Reveal
      gsap.from(".gallery-badge", {
        y: 100,
        opacity: 0,
        rotationX: 90,
        transformPerspective: 1000,
        duration: 1.5,
        ease: "power4.out",
        scrollTrigger: { 
          trigger: ".gallery-badge", 
          start: "top 85%" 
        },
      })

      gsap.from(".gallery-title", {
        y: 120,
        opacity: 0,
        scale: 0.85,
        rotationX: 90,
        transformPerspective: 1500,
        duration: 1.8,
        ease: "power4.out",
        scrollTrigger: { 
          trigger: ".gallery-title", 
          start: "top 80%" 
        },
      })

      // 3D Parallax Zoom on Video Cards
      gsap.from(".gallery-video", {
        scale: 0.6,
        opacity: 0,
        rotationY: -25,
        rotationX: 15,
        transformPerspective: 1500,
        stagger: {
          each: 0.15,
          from: "center",
          grid: "auto",
        },
        duration: 1.4,
        ease: "power4.out",
        scrollTrigger: { 
          trigger: ".gallery-grid", 
          start: "top 75%" 
        },
      })

      // Parallax depth on scroll for each video
      document.querySelectorAll(".gallery-video").forEach((video, index) => {
        const depth = (index % 3) - 1 // Creates -1, 0, 1 pattern
        
        gsap.to(video, {
          y: depth * -50,
          ease: "none",
          scrollTrigger: {
            trigger: video,
            start: "top bottom",
            end: "bottom top",
            scrub: 2,
          },
        })

        // 3D Tilt on hover
        video.addEventListener("mouseenter", function() {
          gsap.to(video, {
            rotationY: 5,
            rotationX: -3,
            scale: 1.08,
            z: 50,
            duration: 0.6,
            ease: "power3.out",
          })
        })

        video.addEventListener("mouseleave", function() {
          gsap.to(video, {
            rotationY: 0,
            rotationX: 0,
            scale: 1,
            z: 0,
            duration: 0.6,
            ease: "power3.out",
          })
        })
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="gallery" className="bg-gradient-to-br from-slate-950 via-teal-950 to-cyan-950 text-white relative overflow-hidden">
      {/* Water Ripple Shader Effect */}
      <div className="absolute inset-0 opacity-30">
        <div className="absolute top-1/4 left-1/4 w-[800px] h-[800px] bg-teal-400 rounded-full blur-[180px] animate-pulse" style={{ animationDuration: '4s' }} />
        <div className="absolute bottom-1/4 right-1/4 w-[700px] h-[700px] bg-cyan-400 rounded-full blur-[160px] animate-pulse" style={{ animationDuration: '5s', animationDelay: '1s' }} />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[600px] h-[600px] bg-teal-300 rounded-full blur-[140px] opacity-40 animate-ping" style={{ animationDuration: '6s' }} />
      </div>

      {/* Animated Grid Overlay */}
      <div className="absolute inset-0 opacity-5" style={{
        backgroundImage: `linear-gradient(rgba(20, 184, 166, 0.1) 1px, transparent 1px),
                         linear-gradient(90deg, rgba(20, 184, 166, 0.1) 1px, transparent 1px)`,
        backgroundSize: '50px 50px'
      }} />

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="gallery-title text-center mb-24">
          <div className="gallery-badge inline-flex items-center gap-3 px-8 py-4 rounded-full bg-gradient-to-r from-teal-500/25 to-cyan-500/25 backdrop-blur-2xl border-2 border-cyan-400/40 mb-10 shadow-2xl shadow-cyan-500/30">
            <Eye className="w-6 h-6 text-cyan-300 animate-pulse" />
            <span className="text-sm font-bold tracking-[0.3em] uppercase bg-gradient-to-r from-cyan-200 to-teal-200 bg-clip-text text-transparent">Portfolio Gallery</span>
          </div>

          <h2 className="font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl xl:text-8xl font-bold mb-8 leading-[1.1]">
            <span className="block bg-gradient-to-r from-teal-100 via-cyan-200 to-teal-100 bg-clip-text text-transparent drop-shadow-[0_0_50px_rgba(6,182,212,0.6)]">
              Transformations
            </span>
            <span className="block bg-gradient-to-r from-cyan-200 via-teal-100 to-cyan-200 bg-clip-text text-transparent drop-shadow-[0_0_50px_rgba(20,184,166,0.6)] mt-2">
              In Motion
            </span>
          </h2>
          <p className="text-xl md:text-2xl text-cyan-100/90 max-w-3xl mx-auto leading-relaxed font-['Montserrat'] font-light">
            Experience the artistry and meticulous precision we bring to every aquatic masterpiece.
          </p>
        </div>

        <div className="gallery-grid grid sm:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-10">
          {videos.map((src, index) => (
            <div
              key={index}
              className="gallery-video relative aspect-video rounded-[2rem] overflow-hidden group cursor-pointer"
              style={{ 
                perspective: "1500px",
                transformStyle: "preserve-3d"
              }}
              onMouseEnter={() => setHoveredIndex(index)}
              onMouseLeave={() => setHoveredIndex(null)}
            >
              {/* 3D Video Frame */}
              <div className="relative w-full h-full rounded-[2rem] overflow-hidden shadow-2xl shadow-cyan-500/40 group-hover:shadow-teal-500/70 transition-all duration-700 border-4 border-teal-500/30 group-hover:border-cyan-400/60">
                <video 
                  src={src} 
                  muted 
                  loop 
                  playsInline 
                  className="w-full h-full object-cover transition-transform duration-1000 group-hover:scale-125" 
                  onMouseOver={(e) => e.currentTarget.play()} 
                  onMouseOut={(e) => e.currentTarget.pause()}
                />
                
                {/* Glass Gradient Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-teal-950/90 via-teal-900/30 to-transparent opacity-70 group-hover:opacity-0 transition-opacity duration-700" />
                
                {/* Play Button with Glow */}
                <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all duration-500">
                  <div className="relative">
                    {/* Neon Glow Ring */}
                    <div className="absolute inset-0 w-28 h-28 bg-gradient-to-br from-cyan-400 to-teal-400 rounded-full blur-2xl opacity-80 animate-pulse" />
                    
                    {/* Play Button */}
                    <div className="relative w-24 h-24 bg-gradient-to-br from-teal-500 via-cyan-500 to-teal-500 backdrop-blur-md rounded-full flex items-center justify-center shadow-2xl shadow-cyan-500/70 group-hover:scale-125 group-hover:rotate-180 transition-all duration-700">
                      <Play className="w-10 h-10 text-white fill-white ml-1" />
                    </div>
                  </div>
                </div>

                {/* 3D Border Effect */}
                <div className="absolute inset-0 rounded-[2rem] ring-4 ring-cyan-400/0 group-hover:ring-cyan-400/60 transition-all duration-700" />
                
                {/* Water Ripple Effect on Hover */}
                {hoveredIndex === index && (
                  <div className="absolute inset-0 pointer-events-none">
                    <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full bg-gradient-radial from-cyan-400/20 via-teal-400/10 to-transparent rounded-full animate-ping" style={{ animationDuration: '2s' }} />
                  </div>
                )}
                
                {/* Corner Accent */}
                <div className="absolute bottom-0 right-0 w-32 h-32 bg-gradient-to-tl from-cyan-500/30 via-teal-500/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-700 rounded-tl-full" />
              </div>

              {/* 3D Depth Shadows */}
              <div className="absolute -inset-2 bg-gradient-to-br from-teal-500/20 to-cyan-500/20 rounded-[2.2rem] blur-xl -z-10 opacity-0 group-hover:opacity-100 transition-opacity duration-700" />
            </div>
          ))}
        </div>
      </div>
    </Section>
  )
}
