"use client"

import { Section } from "./ui/section"
import Image from "next/image"
import { CheckCircle2, Sparkles, Shield, Award, TrendingUp, Waves, Droplets } from "lucide-react"
import { useRef, useEffect } from "react"
import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

if (typeof window !== "undefined") {
  gsap.registerPlugin(ScrollTrigger)
}

export function About() {
  const sectionRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!sectionRef.current) return

    const ctx = gsap.context(() => {
      // 3D Depth Reveal - Bottom to Top
      gsap.from(".about-badge", {
        y: 100,
        opacity: 0,
        rotationX: 90,
        transformPerspective: 1000,
        duration: 1.5,
        ease: "power4.out",
        scrollTrigger: {
          trigger: ".about-badge",
          start: "top 85%",
        },
      })

      gsap.from(".about-title", {
        y: 150,
        opacity: 0,
        rotationX: 90,
        transformPerspective: 1500,
        duration: 1.8,
        ease: "power4.out",
        scrollTrigger: {
          trigger: ".about-title",
          start: "top 80%",
        },
      })

      gsap.from(".about-text", {
        y: 80,
        opacity: 0,
        rotationX: 45,
        transformPerspective: 1000,
        stagger: 0.2,
        duration: 1.2,
        ease: "power3.out",
        scrollTrigger: {
          trigger: ".about-text",
          start: "top 85%",
        },
      })

      // Parallax Image Effect
      gsap.to(".about-image", {
        y: -50,
        ease: "none",
        scrollTrigger: {
          trigger: ".about-image",
          start: "top bottom",
          end: "bottom top",
          scrub: 1,
        },
      })

      gsap.from(".about-image", {
        scale: 0.7,
        opacity: 0,
        rotationY: 15,
        transformPerspective: 2000,
        duration: 2,
        ease: "power4.out",
        scrollTrigger: {
          trigger: ".about-image",
          start: "top 80%",
        },
      })

      gsap.from(".about-feature", {
        x: -100,
        opacity: 0,
        rotationY: -45,
        transformPerspective: 800,
        stagger: 0.18,
        duration: 1.2,
        ease: "back.out(1.5)",
        scrollTrigger: {
          trigger: ".about-features",
          start: "top 85%",
        },
      })

      // Parallax floating card
      gsap.to(".floating-card", {
        y: -30,
        ease: "none",
        scrollTrigger: {
          trigger: ".floating-card",
          start: "top bottom",
          end: "bottom top",
          scrub: 1.5,
        },
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <Section id="about" className="bg-gradient-to-br from-slate-950 via-slate-900 to-teal-950 text-white relative overflow-hidden">
      {/* Glass-Blue Shader Background with Floating Animation */}
      <div className="absolute inset-0 opacity-20">
        <div className="absolute top-0 right-0 w-[600px] h-[600px] bg-teal-500 rounded-full blur-[150px] animate-pulse" />
        <div className="absolute bottom-0 left-0 w-[500px] h-[500px] bg-cyan-500 rounded-full blur-[140px] animate-pulse" />
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[800px] h-[800px] bg-cyan-400 rounded-full blur-[180px] opacity-30" />
      </div>

      {/* Floating Particles Effect - CSS Only */}
      <div className="absolute inset-0 pointer-events-none overflow-hidden">
        {[...Array(15)].map((_, i) => (
          <div
            key={i}
            className="absolute w-2 h-2 bg-teal-400/30 rounded-full blur-sm"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animation: `float ${3 + Math.random() * 4}s ease-in-out infinite`,
              animationDelay: `${Math.random() * 2}s`,
            }}
          />
        ))}
      </div>

      <div ref={sectionRef} className="container mx-auto px-6 relative z-10">
        <div className="grid lg:grid-cols-2 gap-20 items-center">
          <div className="space-y-8">
            <div className="about-badge inline-flex items-center gap-3 px-6 py-3 rounded-full bg-gradient-to-r from-teal-500/20 to-cyan-500/20 backdrop-blur-xl border-2 border-teal-400/30 shadow-2xl shadow-teal-500/30">
              <Waves className="w-5 h-5 text-teal-300 animate-pulse" />
              <span className="text-sm font-bold tracking-[0.3em] uppercase bg-gradient-to-r from-teal-200 to-cyan-200 bg-clip-text text-transparent">Who We Are</span>
            </div>

            <h2 className="about-title font-['Playfair_Display'] text-5xl md:text-6xl lg:text-7xl xl:text-8xl font-bold leading-[1.1]">
              <span className="block bg-gradient-to-r from-teal-100 via-cyan-200 to-teal-100 bg-clip-text text-transparent drop-shadow-[0_0_40px_rgba(20,184,166,0.5)]">
                Crafting Aquatic
              </span>
              <span className="block bg-gradient-to-r from-cyan-200 via-teal-100 to-cyan-200 bg-clip-text text-transparent drop-shadow-[0_0_40px_rgba(6,182,212,0.5)] mt-2">
                Perfection
              </span>
            </h2>

            <p className="about-text text-xl md:text-2xl text-teal-100/90 leading-relaxed font-['Montserrat'] font-light">
              At <span className="font-bold text-cyan-300">Doge Pools</span>, we transcend ordinary pool service — we craft aquatic sanctuaries that redefine luxury living.
            </p>

            <p className="about-text text-lg md:text-xl text-teal-200/70 leading-relaxed font-['Montserrat']">
              Our master technicians blend cutting-edge innovation with artisan-level precision, ensuring your pool embodies pristine elegance and chemical perfection year-round.
            </p>

            <div className="about-features space-y-7 pt-6">
              {[
                { icon: Shield, text: "Certified Master Technicians", color: "from-teal-400 to-cyan-400", desc: "Licensed & Insured Experts" },
                { icon: Droplets, text: "Premium Chemical Systems", color: "from-cyan-400 to-teal-400", desc: "Eco-Friendly Solutions" },
                { icon: Award, text: "Detailed Analytics Reports", color: "from-teal-500 to-cyan-500", desc: "Weekly Performance Tracking" },
                { icon: TrendingUp, text: "100% Satisfaction Promise", color: "from-cyan-500 to-teal-500", desc: "Guaranteed Results" },
              ].map((item, i) => (
                <div key={i} className="about-feature group cursor-pointer">
                  <div className="flex items-center gap-6 bg-white/5 backdrop-blur-lg rounded-2xl p-5 border border-teal-500/20 hover:border-teal-400/50 transition-all duration-500 hover:bg-white/10 hover:shadow-2xl hover:shadow-teal-500/30 hover:-translate-y-2">
                    <div className={`w-16 h-16 rounded-2xl bg-gradient-to-br ${item.color} flex items-center justify-center group-hover:scale-110 group-hover:rotate-12 transition-all duration-500 shadow-xl shadow-teal-500/40`}>
                      <item.icon className="w-8 h-8 text-white" />
                    </div>
                    <div className="flex-1">
                      <h4 className="text-lg font-bold text-white group-hover:text-cyan-300 transition-colors font-['Playfair_Display']">
                        {item.text}
                      </h4>
                      <p className="text-sm text-teal-200/60 font-['Montserrat']">{item.desc}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            <div className="pt-8">
              <a
                href="#services"
                className="group inline-flex items-center gap-4 px-12 py-6 bg-gradient-to-r from-teal-500 via-cyan-500 to-teal-500 text-white font-bold rounded-full hover:shadow-2xl hover:shadow-cyan-500/70 hover:scale-110 transition-all duration-500 text-lg relative overflow-hidden"
              >
                <div className="absolute inset-0 bg-gradient-to-r from-cyan-400 to-teal-400 opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                <TrendingUp className="w-6 h-6 relative z-10 group-hover:rotate-12 transition-transform" />
                <span className="relative z-10">Explore Our Services</span>
                <div className="absolute inset-0 -translate-x-full group-hover:translate-x-full transition-transform duration-1000 bg-gradient-to-r from-transparent via-white/30 to-transparent" />
              </a>
            </div>
          </div>

          <div className="about-image relative">
            <div className="relative h-[700px] rounded-[2.5rem] overflow-hidden shadow-2xl shadow-teal-500/30 group">
              {/* Glass Morphism Overlay */}
              <div className="absolute inset-0 bg-gradient-to-t from-teal-950/90 via-teal-950/20 to-transparent z-10" />
              <Image
                src="/pic.jpeg"
                alt="Luxury Pool Maintenance"
                fill
                className="object-cover transition-transform duration-1000 group-hover:scale-110"
              />
              
              {/* 3D Floating Stats Card */}
              <div className="floating-card absolute bottom-10 left-10 right-10 z-20">
                <div className="bg-gradient-to-br from-slate-900/80 to-teal-900/60 backdrop-blur-2xl rounded-3xl p-8 border-2 border-teal-400/40 shadow-2xl hover:shadow-teal-500/60 hover:scale-105 transition-all duration-700 group/card">
                  <div className="flex items-center gap-6 mb-5">
                    <div className="w-20 h-20 rounded-2xl bg-gradient-to-br from-teal-500 to-cyan-500 flex items-center justify-center shadow-xl shadow-teal-500/50 group-hover/card:rotate-12 transition-transform duration-500">
                      <Award className="w-10 h-10 text-white" />
                    </div>
                    <div>
                      <p className="text-sm font-bold uppercase tracking-[0.2em] text-cyan-400">Established 2024</p>
                      <p className="text-3xl font-['Playfair_Display'] font-bold bg-gradient-to-r from-white to-teal-100 bg-clip-text text-transparent">Industry Pioneers</p>
                    </div>
                  </div>
                  <p className="text-base text-teal-100/90 leading-relaxed font-['Montserrat']">
                    Setting unprecedented standards in luxury pool care through innovation, artistry, and unwavering dedication to perfection.
                  </p>
                </div>
              </div>
            </div>

            {/* Decorative 3D Glows with Parallax */}
            <div className="absolute -top-10 -right-10 w-48 h-48 bg-gradient-to-br from-teal-400 to-cyan-400 rounded-full blur-3xl opacity-50 animate-pulse" />
            <div className="absolute -bottom-10 -left-10 w-48 h-48 bg-gradient-to-br from-cyan-400 to-teal-400 rounded-full blur-3xl opacity-50 animate-pulse" />
            <div className="absolute top-1/2 right-0 w-32 h-32 bg-teal-300 rounded-full blur-2xl opacity-30 animate-ping" style={{ animationDuration: '3s' }} />
          </div>
        </div>
      </div>
    </Section>
  )
}
